module.exports = {
	init: () => {
		API.GLOBAL_ROLE = {
			ADMIN: 5000,
			AMBASSADOR: 3000,
			MODERATOR: 2500,
			PLOT: 750,
			PROMOTER: 500
		};
	},
	kill: () => {
		API.GLOBAL_ROLE = undefined;
	}
};



